import { Component } from '@angular/core';
import { CoinmarketcapService } from './coinmarketcap.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [CoinmarketcapService]
})

export class AppComponent {

  str: any
  litecoin: any 
  bitcoin: any
  ethereum: any
  ripple: any

  title = 'app'

  updatePrices(): any{

    //INSERT current prices into AWS RDS 

  }

  constructor(private coinmarketcap: CoinmarketcapService) { 

    var now = new Date();
    
    setInterval(() => { console.log(now) }, 1000 * 10)  

    this.str = coinmarketcap.getTitle()
    this.title = this.str
    
    //get prices from coinmarketcap.com
    coinmarketcap.getCoinPrice('ripple').subscribe(data => {
      this.ripple = data
    });

    coinmarketcap.getCoinPrice('litecoin').subscribe(data => {
      this.litecoin = data
    });

    coinmarketcap.getCoinPrice('bitcoin').subscribe(data => {
      this.bitcoin = data
    });

    coinmarketcap.getCoinPrice('ethereum').subscribe(data => {
      this.ethereum = data
    });
    
  }

}




